// pages/api/appointments/[id].ts
import type { NextApiRequest, NextApiResponse } from "next";
import { prisma } from "../../../lib/prisma";
import { requireRole } from "../../../server/rbac";

export default async function handler(req: NextApiRequest, res: NextApiResponse) {
  const { id } = req.query;
  const appointmentId = Number(id);
  if (isNaN(appointmentId)) {
    return res.status(400).json({ error: "ID invalide" });
  }

  const session = await requireRole(req, res, ["DOCTOR", "ADMIN", "RECEPTIONIST"]);
  if (!session) return;

  if (req.method === "PATCH") {
    const { status } = req.body;
    if (!["CONFIRMED", "CANCELLED"].includes(status)) {
      return res.status(400).json({ error: "Statut invalide" });
    }

    const appointment = await prisma.appointment.findUnique({
      where: { id: appointmentId },
    });

    if (!appointment) {
      return res.status(404).json({ error: "RDV non trouvé" });
    }

    if (session.user.role === "DOCTOR" && appointment.doctorId !== session.user.id) {
      return res.status(403).json({ error: "Non autorisé" });
    }

    // ✅ CORRECT : UN SEUL OBJET avec where, data, include
    const updated = await prisma.appointment.update({
  where: { id: appointmentId },
  data: { status },
  include: { patient: true, doctor: true },
});


    return res.status(200).json(updated);
  }

  res.setHeader("Allow", ["PATCH"]);
  return res.status(405).json({ error: "Méthode non autorisée" });
}